.movies .movie {
  background-color: white;
  margin: 45px;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;

  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25);
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25);
}
.movie .movie_year {
  margin: 0;
  font-weight: 300;
}

.movie .movie_title {
  margin: 0;
  font-size: 24px;
  color: #2c2d2c;
}

.movie .movie_genres {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year {
  margin-right: 10px;
  font-size: 14px;
}
.movie > a {
  text-decoration: none;
}
